import { createSlice } from "@reduxjs/toolkit";

export const commodity = createSlice({
    name:'city',
    initialState:{
        label:"",
        pinyin:"shenzhen",
        short:"sz",
        value:"AREA|a6649a11-be98-b150"
    },
    reducers:{
        updateCity:(state,action)=>{
            Object.assign(state,action.payload)
        }
    }
})
export const {updateCity} = commodity.actions
export default commodity.reducer