import { Alert, Spin } from 'antd';
import sassLoading from './index.module.scss'

export default function Loading (){
    return (
        <div className={sassLoading.loadingBox}>
           <Spin tip="Loading...">
            <Alert
                className={sassLoading.loadingAlert}
                message="~"
                description="~"
                type="info"
            />
            </Spin>
        </div>
    )
}